.App {
  background: #333;
  min-height: 100vh;
}

.file-container {
  max-width: 200px;
  display: flex;
  margin: 0 auto;
  padding-top: 3rem;
}

input[type="file"] {
  color: #fff;
}

.data-container {
  max-width: 700px;
  margin: 0 auto;
}

.data-header,
.data-row {
  display: inline-flex;
  width: 100%;
}

.data-header-item,
.data-row-item {
  padding: 0.25rem;
  width: 100%;
}

span {
  font-size: 14px;
}
